.navbar {
  height: 80px;
  display: flex;
  width: 100vw;
  background-color: white;
  position: fixed;
  top: 0px;
  z-index: 4;
  background-color: white;
}

.logo {
  height: 46px;
  margin: 16px 0px 0px 46px;
}

.link {
  display: inline-block;
  color: rgb(87, 85, 85);
  font-size: 14px;
  font-family: Whitney, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
    Helvetica, Arial, sans-serif;
  font-weight: bold;
  padding: 30px 18px;
  cursor: pointer;
}

.link:hover {
  border-bottom: 4px solid red;
}

.link1 {
  display: inline-block;
  color: rgb(87, 85, 85);
  font-size: 14px;
  font-family: Whitney, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
    Helvetica, Arial, sans-serif;
  font-weight: bold;
  padding: 30px 18px;
  cursor: pointer;
}

.link1:hover {
  border-bottom: 4px solid orange;
}

.search {
  border: 1px solid rgb(223, 218, 218);
  margin: 20px;
  display: flex;
  border-radius: 4px;
  width: 360px;
  justify-self: end;
}

.searchContainer {
  margin-left: 90px;
}

.input {
  outline: none;
  border: none;
  width: 300px;
}

.text {
  font-size: 12px;
  font-weight: bold;
  font-family: Whitney, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
    Helvetica, Arial, sans-serif;
}

.iconDiv {
  margin-top: 18px;
  width: 60px;
  text-align: center;
}

.iconDiv1 {
  margin-top: 18px;
  width: 60px;
  text-align: center;
  cursor: pointer;
}
