.navbar {
  height: 80px;
  display: flex;
  width: 100vw;
  background-color: white;
  position: fixed;
  top: 0px;
}
.logo {
  height: 46px;
  margin: 16px 0px 0px 46px;
}
.listDiv {
  margin-left: 28%;
  height: 3em;
  margin-top: 1em;
  padding-top: 15px;
}
.orderedList {
  list-style: none;
  display: flex;
  flex-direction: row;
  column-gap: 5px;
  color: #696b79;
  padding: 0;
  font-size: 13px;
  font-weight: 550;
}
.divider {
  border-top: 2px dashed #696b79;
  height: 4px;
  width: 6em;
  margin-top: 0.5em;
  /* border: 1px solid black; */
}
.bag {
  color: rgb(87, 205, 178);
  border-bottom: 3px solid rgb(87, 205, 178);
}
.iconText {
  font-size: 13px;
  color: #696b79;
  font-weight: 550;
}
.iconDiv {
  display: flex;
  flex-direction: row;
  height: 3em;
  margin-top: 1em;
  padding-top: 15px;
  justify-content: flex-end;
  margin-left: 22%;
}
